import { observer } from 'mobx-react-lite'
import React, { useEffect } from 'react'
import { Grid, Segment } from 'semantic-ui-react'
import LoadingComponent from '../../../app/layout/LoadingComponent';
import { useStore } from '../../../app/stores/store';
import LadderMatchItem from './LadderMatchItem';

export default observer(function LadderDashboard() {
  const { matchStore } = useStore();
  const { loadMatches, matches, loadingInitial } = matchStore;

  useEffect(() => {
    if (matches.length <= 1)
    loadMatches()
  }, [matches.length, loadMatches])
  const ladderMatches = matches.slice(48)

  if (loadingInitial) return <LoadingComponent content='Zbieram dane' />
  return (
    <>
      <Grid columns={5} verticalAlign='middle' centered>
        <Grid.Row only='computer'>
          <Grid.Column width={3} >
            <Segment.Group >
              <LadderMatchItem match={ladderMatches[0]} home={'A1'} away={'2B'} title={'1/8 finau #1'} />
              <LadderMatchItem match={ladderMatches[1]} home={'1C'} away={'2D'} title={'1/8 finau #2'} />
            </Segment.Group>
            <Segment.Group >
              <LadderMatchItem match={ladderMatches[2]} home={'1D'} away={'2C'} title={'1/8 finau #3'} />
              <LadderMatchItem match={ladderMatches[3]} home={'1B'} away={'2A'} title={'1/8 finau #4'} />
            </Segment.Group>
          </Grid.Column>

          <Grid.Column width={3}>
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[8]} home={'Gospoda (1)'} away={'Go (1)'} title={'1/4 finau #1'} />
              <LadderMatchItem match={ladderMatches[9]} home={'Gospoda (2)'} away={'Go (2)'} title={'1/4 finau #2'} />
            </Segment.Group>
          </Grid.Column>

          <Grid.Column width={4} >
            <Grid columns={2} centered>
              <Grid.Column width={12}>
                <LadderMatchItem match={ladderMatches[15]} home={'Fina Gospoda'} away={'Fina Go'} title={'FINA'} />
              </Grid.Column>
              <Grid.Column width={12}>
                <LadderMatchItem match={ladderMatches[14]} home={'#3 Gospoda'} away={'#3 Go'} title={'mecz o 3 miejsce'} />
              </Grid.Column>
              <Grid.Column width={12}>
                <Segment.Group >
                  <LadderMatchItem match={ladderMatches[13]} home={'Gospoda'} away={'Go'} title={'1/2 finau #2'} />
                  <LadderMatchItem match={ladderMatches[12]} home={'Gospoda'} away={'Go'} title={'1/2 finau #1'} />
                </Segment.Group>
              </Grid.Column>
            </Grid>
          </Grid.Column>

          <Grid.Column width={3}>
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[10]} home={'Gospoda (3)'} away={'Go (3)'} title={'1/4 finau #3'} />
              <LadderMatchItem match={ladderMatches[11]} home={'Gospoda (4)'} away={'Go (4)'} title={'1/4 finau #4'} />
            </Segment.Group>
          </Grid.Column>

          <Grid.Column width={3}>
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[4]} home={'1E'} away={'2F'} title={'1/8 finau #5'} />
              <LadderMatchItem match={ladderMatches[5]} home={'1G'} away={'2H'} title={'1/8 finau #6'} />
            </Segment.Group>
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[6]} home={'1F'} away={'2E'} title={'1/8 finau #7'} />
              <LadderMatchItem match={ladderMatches[7]} home={'1H'} away={'2G'} title={'1/8 finau #8'} />
            </Segment.Group>
          </Grid.Column>
        </Grid.Row>
      </Grid>

      <Grid columns={3} verticalAlign='middle' centered>
        <Grid.Row only='tablet'>
          <Grid.Column width={4}>
            <Segment.Group >
              <LadderMatchItem match={ladderMatches[0]} home={'A1'} away={'2B'} title={'1/8 finau #1'} />
              <LadderMatchItem match={ladderMatches[1]} home={'1C'} away={'2D'} title={'1/8 finau #2'} />
            </Segment.Group>
            <Segment.Group >
              <LadderMatchItem match={ladderMatches[2]} home={'1D'} away={'2C'} title={'1/8 finau #3'} />
              <LadderMatchItem match={ladderMatches[3]} home={'1B'} away={'2A'} title={'1/8 finau #4'} />
            </Segment.Group>
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[4]} home={'1E'} away={'2F'} title={'1/8 finau #5'} />
              <LadderMatchItem match={ladderMatches[5]} home={'1G'} away={'2H'} title={'1/8 finau #6'} />
            </Segment.Group>
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[6]} home={'1F'} away={'2E'} title={'1/8 finau #7'} />
              <LadderMatchItem match={ladderMatches[7]} home={'1H'} away={'2G'} title={'1/8 finau #8'} />
            </Segment.Group>
          </Grid.Column>
          <Grid.Column width={4} >
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[8]} home={'Gospoda (1)'} away={'Go (1)'} title={'1/4 finau #1'} />
              <LadderMatchItem match={ladderMatches[9]} home={'Gospoda (2)'} away={'Go (2)'} title={'1/4 finau #2'} />
            </Segment.Group>
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[10]} home={'Gospoda (3)'} away={'Go (3)'} title={'1/4 finau #3'} />
              <LadderMatchItem match={ladderMatches[11]} home={'Gospoda (4)'} away={'Go (4)'} title={'1/4 finau #4'} />
            </Segment.Group>
          </Grid.Column>
          <Grid.Column width={8} >
            <Grid columns={2} centered >
              <Grid.Column width={16} >
                <Segment.Group horizontal>
                  <LadderMatchItem match={ladderMatches[13]} home={'Gospoda'} away={'Go'} title={'1/2 finau #2'} />
                  <LadderMatchItem match={ladderMatches[12]} home={'Gospoda'} away={'Go'} title={'1/2 finau #1'} />
                </Segment.Group>
              </Grid.Column>
              <Grid.Column width={9}>
                <LadderMatchItem match={ladderMatches[14]} home={'#3 Gospoda'} away={'#3 Go'} title={'mecz o 3 miejsce'} />
              </Grid.Column>
              <Grid.Column width={12}>
                <LadderMatchItem match={ladderMatches[15]} home={'Fina Gospoda'} away={'Fina Go'} title={'FINA'} />
              </Grid.Column>
            </Grid>
          </Grid.Column>
        </Grid.Row>
      </Grid>

      <Grid columns={2} verticalAlign='middle' centered>
        <Grid.Row only='mobile'>
          <Grid.Column width={8}>
            <Segment.Group >
              <LadderMatchItem match={ladderMatches[0]} home={'A1'} away={'2B'} title={'1/8 finau #1'} />
              <LadderMatchItem match={ladderMatches[1]} home={'1C'} away={'2D'} title={'1/8 finau #2'} />
            </Segment.Group>
            <Segment.Group >
              <LadderMatchItem match={ladderMatches[2]} home={'1D'} away={'2C'} title={'1/8 finau #3'} />
              <LadderMatchItem match={ladderMatches[3]} home={'1B'} away={'2A'} title={'1/8 finau #4'} />
            </Segment.Group>
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[4]} home={'1E'} away={'2F'} title={'1/8 finau #5'} />
              <LadderMatchItem match={ladderMatches[5]} home={'1G'} away={'2H'} title={'1/8 finau #6'} />
            </Segment.Group>
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[6]} home={'1F'} away={'2E'} title={'1/8 finau #7'} />
              <LadderMatchItem match={ladderMatches[7]} home={'1H'} away={'2G'} title={'1/8 finau #8'} />
            </Segment.Group>
          </Grid.Column>
          <Grid.Column width={8} >
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[8]} home={'Gospoda (1)'} away={'Go (1)'} title={'1/4 finau #1'} />
              <LadderMatchItem match={ladderMatches[9]} home={'Gospoda (2)'} away={'Go (2)'} title={'1/4 finau #2'} />
            </Segment.Group>
            <Segment.Group>
              <LadderMatchItem match={ladderMatches[10]} home={'Gospoda (3)'} away={'Go (3)'} title={'1/4 finau #3'} />
              <LadderMatchItem match={ladderMatches[11]} home={'Gospoda (4)'} away={'Go (4)'} title={'1/4 finau #4'} />
            </Segment.Group>
            <Segment.Group >
              <LadderMatchItem match={ladderMatches[13]} home={'Gospoda'} away={'Go'} title={'1/2 finau #2'} />
              <LadderMatchItem match={ladderMatches[12]} home={'Gospoda'} away={'Go'} title={'1/2 finau #1'} />
            </Segment.Group>
            <LadderMatchItem match={ladderMatches[14]} home={'#3 Gospoda'} away={'#3 Go'} title={'mecz o 3 miejsce'} />

            <LadderMatchItem match={ladderMatches[15]} home={'Fina Gospoda'} away={'Fina Go'} title={'FINA'} />
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </>
  )
})
